import React from "react"
import { Link } from "gatsby"

import headerStyles from "./header.module.scss"

const Header = () => {
    return (
        <header className = {headerStyles.header}>
            <h1>
                <Link className={headerStyles.title} to="/">
                    Chris' Portfolio Website
                </Link>
            </h1>
            <nav>
                <ul className={headerStyles.navList}>
                    <li>
                        <Link className={headerStyles.navItem} activeClassName={headerStyles.activeNavItem} to = "/">Home Page</Link>
                    </li>
                    <li>
                        <Link className={headerStyles.navItem} activeClassName={headerStyles.activeNavItem} to = "/about">About Page</Link>
                    </li>
                    <li>
                        <Link className={headerStyles.navItem} activeClassName={headerStyles.activeNavItem} to = "/blog">Blog Page</Link>
                    </li>
                    <li>
                        <Link className={headerStyles.navItem} activeClassName={headerStyles.activeNavItem} to = "/contact">Contact Page</Link>
                    </li>
                </ul>
            </nav>
            
            
            
            
        </header>
    )
}
 
export default Header